<template>
  <div>
    <div class="page-title">Spin </div>
    <p></p>

    <div class="page-sub-title"> Spin </div>
    <Spin></Spin>

    <div class="page-sub-title">sizelargesmall Spin </div>
    <Row>
      <Col span="8">
        <Spin size="small"></Spin>
      </Col>
      <Col span="8">
        <Spin></Spin>
      </Col>
      <Col span="8">
        <Spin size="large"></Spin>
      </Col>
    </Row>

    <div class="page-sub-title">relativeabsolute</div>
    <div class="demo-spin-container">
      <Spin fix></Spin>
    </div>

    <div class="page-sub-title"> Spin </div>
    <Row>
      <Col class="demo-spin-col" span="8">
        <Spin fix>...</Spin>
      </Col>
      <Col class="demo-spin-col" span="8">
        <Spin fix>
          <Icon type="load-c" size="18" class="demo-spin-icon-load"></Icon>
          <div>Loading</div>
        </Spin>
      </Col>
      <Col class="demo-spin-col" span="8">
        <Spin fix>
          <div class="loader">
            <svg class="circular" viewBox="25 25 50 50">
              <circle
                class="path"
                cx="50"
                cy="50"
                r="20"
                fill="none"
                stroke-width="5"
                stroke-miterlimit="10"
              ></circle>
            </svg>
          </div>
        </Spin>
      </Col>
    </Row>

    <div class="page-sub-title"> Spin </div>
    <div class="demo-spin-article">
      <h3></h3>
      <address></address>
      <article>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
      </article>
      <Spin size="large" fix v-if="spinShow"></Spin>
    </div>
    <br>
    <i-switch @on-change="spinShow = !spinShow"></i-switch>

    <div class="page-sub-title"> $Spin  Render </div>
    <div>
      <Button type="primary" @click="handleSpinShow">3</Button>
      <Button type="primary" style="margin-left: 10px;" @click="handleSpinCustom"></Button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      spinShow: true
    };
  },
  methods: {
    handleSpinShow() {
      this.$Spin.show();
      setTimeout(() => {
        this.$Spin.hide();
      }, 3000);
    },
    handleSpinCustom() {
      this.$Spin.show({
        render: h => {
          return h("div", [
            h("Icon", {
              class: "demo-spin-icon-load",
              props: {
                type: "load-c",
                size: 18
              }
            }),
            h("div", "Loading")
          ]);
        }
      });
      setTimeout(() => {
        this.$Spin.hide();
      }, 3000);
    }
  }
};
</script>
<style lang="less" scoped>
.demo-spin-container {
  display: inline-block;
  width: 200px;
  height: 100px;
  position: relative;
  border: 1px solid #eee;
}
.demo-spin-col {
  height: 100px;
  position: relative;
  border: 1px solid #eee;
}
svg:not(:root) {
  overflow: hidden;
}
.circular {
  animation: rotate 2s linear infinite;
  height: 100%;
  transform-origin: center center;
  width: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.path {
  stroke-dasharray: 1, 200;
  stroke-dashoffset: 0;
  animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
  stroke-linecap: round;
}
.loader {
  width: 30px;
  height: 30px;
  position: relative;
  margin: 0 auto;
}
@keyframes dash {
  0% {
    stroke-dasharray: 1, 200;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -35;
  }

  to {
    stroke-dasharray: 89, 200;
    stroke-dashoffset: -124;
  }
}
@keyframes color {
  0%,
  to {
    stroke: #d62d20;
  }

  40% {
    stroke: #0057e7;
  }

  66% {
    stroke: #008744;
  }

  80%,
  90% {
    stroke: #ffa700;
  }
}
@keyframes rotate {
  to {
    transform: rotate(1turn);
  }
}
.demo-spin-article {
  width: 400px;
  height: 200px;
  padding: 10px;
  text-align: center;
  position: relative;
  border: 1px solid #eee;
}
</style>
<style lang="less">
.demo-spin-icon-load {
  animation: ani-demo-spin 1s linear infinite;
}
@keyframes ani-demo-spin {
  from {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>