    <p></p>

    <div class="page-sub-title">
       v-model 
      <br>value Optionvalue
      <br>Select style 
      <br>updownEnterEsc
    </div>
    <Select v-model="model1" style="width:200px">
      <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>

    <div class="page-sub-title">sizelargesmall</div>
    <Select v-model="model2" size="small" style="width:100px">
      <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>
    <Select v-model="model3" style="width:100px;margin-left:10px;">
      <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>
    <Select v-model="model4" size="large" style="width:100px;margin-left:10px;">
      <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>

    <div class="page-sub-title">SelectdisabledOptiondisabled</div>
    <Select v-model="model5" disabled style="width:200px">
      <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>
    <Select v-model="model6" style="width:200px;margin-left:10px;">
      <Option value="beijing">New York</Option>
      <Option value="shanghai" disabled>London</Option>
      <Option value="shenzhen">Sydney</Option>
    </Select>

    <div class="page-sub-title">clearable</div>
    <Select v-model="model8" clearable style="width:200px">
      <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>

    <div class="page-sub-title">OptionGroup</div>
    <Select v-model="model7" style="width:200px">
      <OptionGroup label="Hot Cities">
        <Option v-for="item in cityList1" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </OptionGroup>
      <OptionGroup label="Other Cities">
        <Option v-for="item in cityList2" :value="item.value" :key="item.value">{{ item.label }}</Option>
      </OptionGroup>
    </Select>

    <div class="page-sub-title"></div>
    <p>
      Optionlabel
      
    </p>
    <p> slot  slotlabel labelvalue</p>
    <br>
    <Select v-model="model9" style="width:200px">
      <Option value="New York" label="">
        <span>New York</span>
        <span style="float:right;color:#ccc">America</span>
      </Option>
      <Option value="London" label="">
        <span>London</span>
        <span style="float:right;color:#ccc">U.K.</span>
      </Option>
      <Option value="Sydney" label="">
        <span>Sydney</span>
        <span style="float:right;color:#ccc">Australian</span>
      </Option>
    </Select>

    <div class="page-sub-title">multiplemodel </div>
    <Select v-model="model10" multiple style="width:260px">
      <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
    </Select>

    <div class="page-sub-title">
      filterable
      Delete
    </div>
    <Row>
      <Col span="12" style="padding-right:10px">
        <Select v-model="model11" filterable>
          <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
      <Col span="12">
        <Select v-model="model12" filterable multiple>
          <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
    </Row>

    <div class="page-sub-title"></div>
    <p>
       filterableremoteremote-methodloading  props loading 
      remote-method  Option  key label 
    </p>
    <Row>
      <Col span="12" style="padding-right:10px">
        <Select
          v-model="model13"
          filterable
          remote
          :remote-method="remoteMethod1"
          :loading="loading1"
        >
          <Option
            v-for="(option, index) in options1"
            :value="option.value"
            :key="index"
          >{{option.label}}</Option>
        </Select>
      </Col>
      <Col span="12">
        <Select
          v-model="model14"
          multiple
          filterable
          remote
          :remote-method="remoteMethod2"
          :loading="loading2"
        >
          <Option
            v-for="(option, index) in options2"
            :value="option.value"
            :key="index"
          >{{option.label}}</Option>
        </Select>
      </Col>
    </Row>

    <div style="padding:100px;"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      cityList: [
        {
          value: "New York",
          label: "New York"
        },
        {
          value: "London",
          label: "London"
        },
        {
          value: "Sydney",
          label: "Sydney"
        },
        {
          value: "Ottawa",
          label: "Ottawa"
        },
        {
          value: "Paris",
          label: "Paris"
        },
        {
          value: "Canberra",
          label: "Canberra"
        }
      ],
      model1: "London",
      model2: "",
      model3: "",
      model4: "",
      model5: "",
      model6: "",
      model8: "",
      cityList1: [
        {
          value: "New York",
          label: "New York"
        },
        {
          value: "London",
          label: "London"
        },
        {
          value: "Sydney",
          label: "Sydney"
        }
      ],
      cityList2: [
        {
          value: "Ottawa",
          label: "Ottawa"
        },
        {
          value: "Paris",
          label: "Paris"
        },
        {
          value: "Canberra",
          label: "Canberra"
        }
      ],
      model7: "",
      model9: "",
      model10: [],
      model11: "",
      model12: [],
      model13: "",
      loading1: false,
      options1: [],
      model14: [],
      loading2: false,
      options2: [],
      list: [
        "Alabama",
        "Alaska",
        "Arizona",
        "Arkansas",
        "California",
        "Colorado",
        "Connecticut",
        "Delaware",
        "Florida",
        "Georgia",
        "Hawaii",
        "Idaho",
        "Illinois",
        "Indiana",
        "Iowa",
        "Kansas",
        "Kentucky",
        "Louisiana",
        "Maine",
        "Maryland",
        "Massachusetts",
        "Michigan",
        "Minnesota",
        "Mississippi",
        "Missouri",
        "Montana",
        "Nebraska",
        "Nevada",
        "New hampshire",
        "New jersey",
        "New mexico",
        "New york",
        "North carolina",
        "North dakota",
        "Ohio",
        "Oklahoma",
        "Oregon",
        "Pennsylvania",
        "Rhode island",
        "South carolina",
        "South dakota",
        "Tennessee",
        "Texas",
        "Utah",
        "Vermont",
        "Virginia",
        "Washington",
        "West virginia",
        "Wisconsin",
        "Wyoming"
      ]
    };
  },
  methods: {
    remoteMethod1(query) {
      if (query !== "") {
        this.loading1 = true;
        setTimeout(() => {
          this.loading1 = false;
          const list = this.list.map(item => {
            return {
              value: item,
              label: item
            };
          });
          this.options1 = list.filter(
            item => item.label.toLowerCase().indexOf(query.toLowerCase()) > -1
          );
        }, 200);
      } else {
        this.options1 = [];
      }
    },
    remoteMethod2(query) {
      if (query !== "") {
        this.loading2 = true;
        setTimeout(() => {
          this.loading2 = false;
          const list = this.list.map(item => {
            return {
              value: item,
              label: item
            };
          });
          this.options2 = list.filter(
            item => item.label.toLowerCase().indexOf(query.toLowerCase()) > -1
          );
        }, 200);
      } else {
        this.options2 = [];
      }
    }
  }
};
</script>
