<template>
  <div>
    <div class="page-title">Switch </div>
    <p></p>
    <div class="page-sub-title"></div>
    <ISwitch v-model="switch1" @on-change="change"></ISwitch>

    <div class="page-sub-title">size large  small </div>
    <div class="switchbox">
      <ISwitch size="large"></ISwitch>
      <ISwitch></ISwitch>
      <ISwitch size="small"></ISwitch>
    </div>
    <br><br>

    <div class="page-sub-title">2 large</div>
    <ISwitch>
        <span slot="open"></span>
        <span slot="close"></span>
    </ISwitch>
    <ISwitch style="margin-left:10px;">
        <Icon type="android-done" slot="open"></Icon>
        <Icon type="android-close" slot="close"></Icon>
    </ISwitch>
    <br><br>
    <ISwitch size="large">
        <span slot="open"></span>
        <span slot="close"></span>
    </ISwitch>
    <ISwitch size="large" style="margin-left:10px;">
        <span slot="open">ON</span>
        <span slot="close">OFF</span>
    </ISwitch>

    <div class="page-sub-title"></div>
    <ISwitch :disabled="disabled" style="margin-right:10px;"></ISwitch>
    <Button type="primary" @click="disabled = !disabled">Toggle Disabled</Button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      switch1: false,
      disabled: true
    };
  },
  methods: {
    change(status) {
      this.$Message.info("" + status);
    }
  }
};
</script>
<style lang="less" scoped>
.switchbox {
  .ui-switch {
    + .ui-switch {
      margin-left: 10px;
    }
  }
}
</style>