<template>
  <div>
    <div class="page-title">Modal </div>

    <div class="page-sub-title">visible /  v-model </div>
    <Button type="primary" @click="modal1 = true"></Button>
    <Modal v-model="modal1" middle title="Modal" @on-ok="ok" @on-cancel="cancel">
      <p></p>
      <p></p>
      <p></p>
    </Modal>

    <div class="page-sub-title">
      Modal  API  Slot Modal 
      
    </div>
    <Button @click="modal2 = true">Custom header and footer</Button>
    <Modal v-model="modal2" width="360">
      <p slot="header" style="color:#f60;text-align:center">
        <Icon type="information-circled"></Icon>
        <span></span>
      </p>
      <div style="text-align:center">
        <p> 10 </p>
        <p></p>
      </div>
      <div slot="footer">
        <Button type="error" size="large" long :loading="modal_loading" @click="del">Delete</Button>
      </div>
    </Modal>
    <Button @click="modal3 = true">No title bar</Button>
    <Modal v-model="modal3">
      <p>Content of dialog</p>
      <p>Content of dialog</p>
      <p>Content of dialog</p>
    </Modal>
    <Button @click="modal4 = true">Internationalization</Button>
    <Modal v-model="modal4" title="Modal Title" ok-text="OK" cancel-text="Cancel">
      <p>Something...</p>
      <p>Something...</p>
      <p>Something...</p>
    </Modal>
    <Button @click="modal5 = true">Set the width</Button>
    <Modal v-model="modal5" title="Custom width" width="300">
      <p>Customize width, unit px, default 520px.</p>
      <p>
        The width of the dialog box is responsive, and the width becomes
        <code>auto</code> when the screen size is less than 768px.
      </p>
    </Modal>

    <div class="page-sub-title">
      Modalloading
       loading 
    </div>
    <Button type="primary" @click="modal6 = true">Display dialog box</Button>
    <Modal v-model="modal6" title="Title" :loading="loading" @on-ok="asyncOK">
      <p>After you click ok, the dialog box will close in 2 seconds.</p>
    </Modal>

    <div class="page-sub-title"></div>
    <Button @click="modal7 = true">Disable upper right corner (including Esc key)</Button>
    <Modal title="Title" v-model="modal7" :closable="false">
      <p>Content of dialog</p>
      <p>Content of dialog</p>
      <p>Content of dialog</p>
    </Modal>
    <Button @click="modal8 = true">Disable mask layer closure</Button>
    <Modal title="Title" v-model="modal8" :mask-closable="false">
      <p>Content of dialog</p>
      <p>Content of dialog</p>
      <p>Content of dialog</p>
    </Modal>

    <div class="page-sub-title">
      
       Vue 
    </div>
    <Button @click="instance('info')">Info</Button>
    <Button @click="instance('success')">Success</Button>
    <Button @click="instance('warning')">Warning</Button>
    <Button @click="instance('error')">Error</Button>
    <div class="page-sub-title"></div>
    <Button @click="instance('confirm')">Confirm</Button>
    <Button @click="custom">Custom button text</Button>
    <Button @click="async">Asynchronous closing</Button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      modal1: false,
      modal2: false,
      modal_loading: false,
      modal3: false,
      modal4: false,
      modal5: false,
      modal6: false,
      loading: true,
      modal7: false,
      modal8: false
    };
  },
  methods: {
    ok() {
      this.$Message.info("Clicked ok");
    },
    cancel() {
      this.$Message.info("Clicked cancel");
    },
    del() {
      this.modal_loading = true;
      setTimeout(() => {
        this.modal_loading = false;
        this.modal2 = false;
        this.$Message.success("Successfully delete");
      }, 2000);
    },
    asyncOK() {
      setTimeout(() => {
        this.modal6 = false;
      }, 2000);
    },
    instance(type) {
      const title = "";
      const content =
        "<p></p><p></p><p></p>";
      switch (type) {
        case "info":
          this.$Modal.info({
            title: title,
            content: content
          });
          break;
        case "success":
          this.$Modal.success({
            title: title,
            content: content
          });
          break;
        case "warning":
          this.$Modal.warning({
            title: title,
            content: content
          });
          break;
        case "error":
          this.$Modal.error({
            title: title,
            content: content
          });
          break;
        case "confirm":
          this.$Modal.confirm({
            title: title,
            content: content,
            onOk: () => {
              this.$Message.info("Clicked ok");
            },
            onCancel: () => {
              this.$Message.info("Clicked cancel");
            }
          });
          break;
      }
    },
    custom() {
      this.$Modal.confirm({
        title: "Title",
        content: "<p>Content of dialog</p><p>Content of dialog</p>",
        okText: "OK",
        cancelText: "Cancel"
      });
    },
    async() {
      this.$Modal.confirm({
        title: "Title",
        content: "<p>The dialog box will be closed after 2 seconds</p>",
        loading: true,
        onOk: () => {
          setTimeout(() => {
            this.$Modal.remove();
            this.$Message.info("Asynchronously close the dialog box");
          }, 2000);
        }
      });
    }
  }
};
</script>
