<template>
  <div>
    <div class="page-title">Transfer </div>
    <p></p>
    <div class="page-sub-title">Transfer  API :</div>
    <ul class="list">
      <li>
        <p>:data key </p>
      </li>
      <li>
        <p>:target-keys key Transfer  key </p>
      </li>
      <li>
        <p>:render-format label  key </p>
      </li>
      <li>
        <p>@on-change</p>
      </li>
    </ul>

    <div
      class="page-sub-title"
    > datatarget-keys render-format  on-change </div>
    <Transfer
      :data="data1"
      :target-keys="targetKeys1"
      :render-format="render1"
      @on-change="handleChange1"
    ></Transfer>

    <div class="page-sub-title"> filterable </div>
    <Transfer
      :data="data2"
      :target-keys="targetKeys2"
      filterable
      :filter-method="filterMethod"
      @on-change="handleChange2"
    ></Transfer>

    <div class="page-sub-title"> API</div>
    <Transfer
      :data="data3"
      :target-keys="targetKeys3"
      :boxStyle="boxStyle"
      :list-style="listStyle"
      :render-format="render3"
      :operations="['To left','To right']"
      filterable
      @on-change="handleChange3"
    >
      <div :style="{float: 'right', margin: '5px'}">
        <Button type="ghost" size="small" @click="reloadMockData">Refresh</Button>
      </div>
    </Transfer>

    <div class="page-sub-title"> render-format </div>
    <Transfer
      :data="data4"
      :target-keys="targetKeys4"
      :render-format="render4"
      @on-change="handleChange4"
    ></Transfer>
  </div>
</template>
<script>
export default {
  data() {
    return {
      data1: this.getMockData(),
      targetKeys1: this.getTargetKeys(),
      data2: this.getMockData(),
      targetKeys2: this.getTargetKeys(),
      data3: this.getMockData(),
      targetKeys3: this.getTargetKeys(),
      data4: this.getMockData(),
      targetKeys4: this.getTargetKeys(),
      listStyle: {
        height: "250px"
      },
      boxStyle: {
        width: "250px"
      }
    };
  },
  methods: {
    getMockData() {
      let mockData = [];
      for (let i = 1; i <= 20; i++) {
        mockData.push({
          key: i.toString(),
          label: "Content " + i,
          description: "The desc of content  " + i,
          disabled: Math.random() * 3 < 1
        });
      }
      return mockData;
    },
    getTargetKeys() {
      return this.getMockData()
        .filter(() => Math.random() * 2 > 1)
        .map(item => item.key);
    },
    render1(item) {
      return item.label;
    },
    handleChange1(newTargetKeys, direction, moveKeys) {
      console.log(newTargetKeys);
      console.log(direction);
      console.log(moveKeys);
      this.targetKeys1 = newTargetKeys;
    },
    handleChange2(newTargetKeys) {
      this.targetKeys2 = newTargetKeys;
    },
    filterMethod(data, query) {
      return data.filter(_ => _.label.indexOf(query) !== -1);
    },
    handleChange3(newTargetKeys) {
      this.targetKeys3 = newTargetKeys;
    },
    render3(item) {
      return item.label + " - " + item.description;
    },
    reloadMockData() {
      this.data3 = this.getMockData();
      this.targetKeys3 = this.getTargetKeys();
    },
    handleChange4(newTargetKeys) {
      this.targetKeys4 = newTargetKeys;
    },
    render4(item) {
      return item.label + " - " + item.description;
    }
  }
};
</script>
<style lang="less" scoped>
.list {
  padding-left: 30px;
  line-height: 2.2;
}
</style>