<template>
  <div>
    <div class="page-title">Tabs </div>
    <p></p>

    <div class="page-sub-title">
      value  TabPane  name name  0 
       v-model 
    </div>
    <Tabs value="name1">
      <TabPane label="" name="name1"></TabPane>
      <TabPane label="" name="name2"></TabPane>
      <TabPane label="" name="name3"></TabPane>
    </Tabs>

    <div class="page-sub-title"></div>
    <Tabs>
      <TabPane label=""></TabPane>
      <TabPane label="" disabled></TabPane>
      <TabPane label=""></TabPane>
    </Tabs>

    <div class="page-sub-title"> icon</div>
    <Tabs>
      <TabPane label="macOS" icon="social-apple"></TabPane>
      <TabPane label="Windows" icon="social-windows"></TabPane>
      <TabPane label="Linux" icon="social-tux"></TabPane>
    </Tabs>

    <div class="page-sub-title"> size  small  type  line </div>
    <Tabs size="small">
      <TabPane label=""></TabPane>
      <TabPane label=""></TabPane>
      <TabPane label=""></TabPane>
    </Tabs>

    <div class="page-sub-title"> type  card </div>
    <Tabs type="card">
      <TabPane label=""></TabPane>
      <TabPane label=""></TabPane>
      <TabPane label=""></TabPane>
    </Tabs>

    <div class="page-sub-title">
       closable  type  card 
       @on-tab-remove 
    </div>
    <Tabs type="card" closable @on-tab-remove="handleTabRemove">
      <TabPane label="" v-if="tab0"></TabPane>
      <TabPane label="" v-if="tab1"></TabPane>
      <TabPane label="" v-if="tab2"></TabPane>
    </Tabs>

    <div class="page-sub-title"> label  Render </div>
    <Tabs value="name1">
      <TabPane :label="label" name="name1"></TabPane>
      <TabPane label="" name="name2"></TabPane>
      <TabPane label="" name="name3"></TabPane>
    </Tabs>

    <div class="page-sub-title"> slot extra </div>
    <Tabs type="card">
      <TabPane v-for="tab in tabs" :key="tab" :label="'' + tab">{{ tab }}</TabPane>
      <Button type="ghost" @click="handleTabsAdd" size="small" slot="extra"></Button>
    </Tabs>

    <div class="page-sub-title"> animated  false </div>
    <Tabs :animated="false">
      <TabPane label=""></TabPane>
      <TabPane label=""></TabPane>
      <TabPane label=""></TabPane>
    </Tabs>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tab0: true,
      tab1: true,
      tab2: true,
      tabs: 2,
      label: h => {
        return h("div", [
          h("span", ""),
          h("Badge", {
            props: {
              count: 3
            }
          })
        ]);
      }
    };
  },
  methods: {
    handleTabRemove(name) {
      this["tab" + name] = false;
    },
    handleTabsAdd() {
      this.tabs++;
    }
  }
};
</script>
<style lang="less">
</style>