<template>
  <div>
    <div class="page-title">Steps </div>
    <p></p>

    <div class="page-sub-title">current</div>
    <Steps :current="1">
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
    </Steps>

    <div class="page-sub-title">sizesmall</div>
    <Steps :current="2" size="small">
      <Step title=""></Step>
      <Step title=""></Step>
      <Step title=""></Step>
      <Step title=""></Step>
    </Steps>

    <div class="page-sub-title">Stepicon</div>
    <Steps :current="1">
      <Step title="" icon="person-add"></Step>
      <Step title="" icon="camera"></Step>
      <Step title="" icon="email"></Step>
    </Steps>

    <div class="page-sub-title"></div>
    <p> {{ current + 1 }} </p>
    <Steps style="margin:15px 0;" :current="current">
      <Step title="1"></Step>
      <Step title="2"></Step>
      <Step title="3"></Step>
      <Step title="4"></Step>
    </Steps>
    <Button type="primary" @click="next">Next step</Button>

    <div class="page-sub-title">directionvertical</div>
    <Steps :current="2" direction="vertical">
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
    </Steps>

    <div class="page-sub-title">Stepsstatuserror</div>
    <Steps :current="1" status="error">
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
      <Step title="" content=""></Step>
    </Steps>

    <div style="padding:100px;"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      current: 0
    };
  },
  methods: {
    next() {
      if (this.current == 3) {
        this.current = 0;
      } else {
        this.current += 1;
      }
    }
  }
};
</script>
