<template>
  <div>
    <div class="page-title">LoadingBar </div>
    <p></p>
    <br>
    <p>
      LoadingBar 
      AjaxLoadingBar 
      update()API
    </p>

    <div
      class="page-sub-title"
    > Start  Finish start()finish()error()</div>
    <Button @click="start"></Button>
    <Button style="margin-left:4px;" @click="finish"></Button>
    <Button style="margin-left:4px;" @click="error"></Button>
    <Button style="margin-left:4px;" @click="destroy"></Button>

  <div class="page-sub-title"></div>
  <ul class="list">
    <li>this.$Loading.start()</li>
    <li>this.$Loading.finish()</li>
    <li>this.$Loading.error()</li>
    <li>this.$Loading.update(percent)</li>
  </ul>

  <div class="page-sub-title"></div>
  <ul class="list">
    <li>this.$Loading.config(options)</li>
    <li>this.$Loading.destroy()</li>
  </ul>
  <br><br>
  <p>Vue.LoadingBar  this.$LoadingAPI</p>
  </div>
</template>
<script>
export default {
  methods: {
    start() {
      this.$Loading.start();
    },
    finish() {
      this.$Loading.finish();
    },
    error() {
      this.$Loading.error();
    },
    destroy() {
      this.$Loading.destroy()
    }
  }
};
</script>
<style lang="less" scoped>
.list {
  padding-left: 30px;
  line-height: 2;
  font-size: 14px;
}
