<template>
  <div>
    <div class="page-title">Message </div>
    <Button @click="openInfo"></Button>
    <Button @click="openSuccess"></Button>
    <Button @click="openWarning"></Button>
    <Button @click="openError"></Button>
    <Button @click="openLoading"></Button>
  </div>
</template>
<script>
export default {
  methods: {
    openInfo() {
      this.$Message.info('')
    },
    openSuccess() {
      this.$Message.success('')
    },
    openWarning() {
      this.$Message.warning('')
    },
    openError() {
      this.$Message.error('')
    },
    openLoading() {
      this.$Message.loading('')
    }
  }
}
</script>
